import { useForm } from 'react-hook-form';
import { ToastContainer, toast } from 'react-toastify';

import { useModalContext } from '../../../context/ModalContext';

import 'react-toastify/dist/ReactToastify.css';

import IconPassword from 'svgImage/IconPassword';
import IconEmail from 'svgImage/IconEmail';
import FormErrorMessage from '../FormErrorMessage';

import RegisterForm from '../RegisterForm/RegisterForm';
import s from './LoginForm.module.css';
import 'animate.css';

export default function LoginForm() {
  const { setModalContent } = useModalContext();
  const {
    register,
    handleSubmit,
    formState: { errors },
    reset,
  } = useForm();

  const handleClick = errors => {
    if (errors.name)
      toast.error(errors.name.message, {
        position: toast.POSITION.TOP_RIGHT,
      });

    if (errors.password)
      toast.error(errors.password.message, {
        position: toast.POSITION.TOP_RIGHT,
      });
  };
  const onSubmit = data => {
    console.log(data);
    reset();
  };

  return (
    <>
      <ToastContainer />
      <div className="animate__animated animate__zoomIn animate__faster">
        <form onSubmit={handleSubmit(onSubmit)} className={s.form}>
          <div className={s.formTitleWrap}>
            <h1 className={s.title}>  </h1>

            <button
              type="button"
              className={s.registerLink}
              onClick={() => setModalContent(<RegisterForm />)}
            >
              
            </button>
          </div>
          <label className={s.label}>
            <span className={s.labelTitle}></span>
            <div className={s.inputWrap}>
              <div className={s.svgWrap}>
                <IconEmail />
              </div>

              <input
                {...register('email', {
                  required: '    ',
                  pattern: {
                    value: /^[\w.-]+@[a-zA-Z_-]+?(?:\.[a-zA-Z]{2,6})+$/,
                    message: `   :\n 
                    -   ;\n
                    -  "@".`,
                  },
                })}
                type="text"
                name="email"
                placeholder="Email"
                autoComplete="email"
                className={`${s.input} ${errors.email ? s.invalid : s.valid}`}
              />
            </div>

            {errors.email && (
              <FormErrorMessage errorText={errors.email.message} />
            )}
          </label>
          <label className={s.label}>
            <span className={s.labelTitle}></span>
            <div className={s.inputWrap}>
              <div className={s.svgWrap}>
                <IconPassword />
              </div>

              <input
                {...register('password', {
                  required: '  ',
                  pattern: {
                    value: /^(?=.*[A-Za-z])(?=.*\d)[A-Za-z\d]{4,16}$/,
                    message: `    :\n
        -   4  16 ;\n
        -     ;\n
        - '       .`,
                  },
                })}
                type="password"
                name="password"
                placeholder="Password"
                autoComplete="password"
                className={`${s.input} ${
                  errors.password ? s.invalid : s.valid
                }`}
              />
            </div>

            {errors.password && (
              <FormErrorMessage errorText={errors.password.message} />
            )}
          </label>

          <input
            className={s.submit}
            type="submit"
            value=""
            onClick={() => handleClick(errors)}
          />
        </form>
      </div>
    </>
  );
}
