import { useDispatch } from 'react-redux';
import { useForm } from 'react-hook-form';
import { ToastContainer, toast } from 'react-toastify';
import 'react-toastify/dist/ReactToastify.css';

import IconPassword from 'svgImage/IconPassword';
import IconEmail from 'svgImage/IconEmail';
import IconPhone from 'svgImage/IconPhone';
import FormErrorMessage from '../FormErrorMessage';

import authOperations from 'redux/auth/auth-operation';
import s from './RegisterForm.module.css';

export default function RegisterForm() {
  const dispatch = useDispatch();
  const {
    register,
    handleSubmit,
    formState: { errors },
    reset,
  } = useForm();

  const handleClick = errors => {
    if (errors.name)
      toast.error(errors.name.message, {
        position: toast.POSITION.TOP_RIGHT,
      });

    if (errors.email)
      toast.error(errors.email.message, {
        position: toast.POSITION.TOP_RIGHT,
      });

    if (errors.password)
      toast.error(errors.password.message, {
        position: toast.POSITION.TOP_RIGHT,
      });
  };
  const onSubmit = data => {
    dispatch(authOperations.register(data));
    reset();
  };

  return (
    <>
      <ToastContainer />
      <form onSubmit={handleSubmit(onSubmit)} className={s.form}>
        <h1 className={s.title}> </h1>

        <label className={s.label}>
          <span className={s.labelTitle}></span>
          <div className={s.inputWrap}>
            <div className={s.svgWrap}>
              <IconPhone />
            </div>

            <input
              {...register('phone', {
                required: '   ',
                pattern: {
                  value: /^(?:\+380|0)\d{9}$/,
                  message: `     :\n 
                  0991112233  +380991112233;
                  `,
                },
              })}
              type="text"
              name="phone"
              placeholder="Phone"
              autoComplete="phone"
              className={`${s.input} ${errors.phone ? s.invalid : s.valid}`}
            />
          </div>
          {errors.phone && (
            <FormErrorMessage errorText={errors.phone.message} />
          )}
        </label>
        <label className={s.label}>
          <span className={s.labelTitle}></span>
          <div className={s.inputWrap}>
            <div className={s.svgWrap}>
              <IconEmail />
            </div>
            <input
              {...register('email', {
                required: '   ',
                pattern: {
                  value: /^[\w.-]+@[a-zA-Z_-]+?(?:\.[a-zA-Z]{2,6})+$/,
                  message: `   :\n 
                    -   ;\n
                    -  "@".`,
                },
              })}
              type="text"
              name="email"
              placeholder="Email"
              autoComplete="email"
              className={`${s.input} ${errors.email ? s.invalid : s.valid}`}
            />
          </div>
          {errors.email && (
            <FormErrorMessage errorText={errors.email.message} />
          )}
        </label>
        <label className={s.label}>
          <span className={s.labelTitle}></span>
          <div className={s.inputWrap}>
            <div className={s.svgWrap}>
              <IconPassword />
            </div>
            <input
              {...register('password', {
                required: ' ',
                pattern: {
                  value: /^(?=.*[A-Za-z])(?=.*\d)[A-Za-z\d]{4,16}$/,
                  message: `    :\n
        -   4  16 ;\n
        -     ;\n
        - '       .`,
                },
              })}
              type="password"
              name="password"
              placeholder="Password"
              autoComplete="password"
              className={`${s.input} ${errors.password ? s.invalid : s.valid}`}
            />
          </div>
          {errors.password && (
            <FormErrorMessage errorText={errors.password.message} />
          )}
        </label>

        <input
          className={s.submit}
          type="submit"
          value=""
          onClick={() => handleClick(errors)}
        />
      </form>
    </>
  );
}
