import InputLabel from '@mui/material/InputLabel';
import MenuItem from '@mui/material/MenuItem';
import FormControl from '@mui/material/FormControl';
import Select from '@mui/material/Select';
import DehazeIcon from '@mui/icons-material/Dehaze';

import { useDispatch } from 'react-redux';
import { updateToolsFilter } from 'redux/tools/toolsFilterSlice';
import { useState } from 'react';
import { updateTools } from 'redux/tools/toolsSlice';

export default function SelectSmall() {
  const [toolType, setToolType] = useState('');

  const dispatch = useDispatch();

  const handleChange = event => {
    setToolType(event.target.value);
    dispatch(updateTools([]));
    const queryParams = { type: event.target.value, page: 1 };
    dispatch(updateToolsFilter(queryParams));
  };

  const selectStyles = {
    color: 'white',
    '& .MuiSelect-icon': {
      color: 'white', //  
      transition: 'color 0.25',
    },
    '&:hover .MuiSelect-icon': {
      color: '#DA9022', //    
    },
    '&.Mui-focused .MuiSelect-icon': {
      color: '#DA9022', //    
    },
    '& .MuiOutlinedInput-notchedOutline': {
      borderColor: 'white', //  
      transition: 'border-color 0.25s',
    },
    '&:hover .MuiOutlinedInput-notchedOutline': {
      borderColor: '#DA9022', //    
    },
    '&.Mui-focused .MuiOutlinedInput-notchedOutline': {
      borderColor: '#DA9022', //    
    },
  };

  return (
    <FormControl
      sx={{
        m: 1,
        minWidth: 200,
        height: 40,
        marginLeft: '20px',
      }}
      size="small"
    >
      <InputLabel
        id="demo-select-small-label"
        sx={{
          background: '#707070',
          paddingRight: 1,
          color: 'white', //  
          '&:hover': {
            color: '#DA9022', //    
          },
          '&.Mui-focused': {
            color: '#DA9022', //    
          },
        }}
      >
         
      </InputLabel>
      <Select
        labelId="demo-select-small-label"
        id="demo-select-small"
        value={toolType}
        label="Tool type"
        onChange={handleChange}
        IconComponent={DehazeIcon}
        className="select"
        sx={selectStyles}
      >
        <MenuItem value="">
          <em> </em>
        </MenuItem>
        <MenuItem value={'power tool'}> </MenuItem>
        <MenuItem value={'hand tool'}> </MenuItem>
        <MenuItem value={'Gasoline-powered tools'}> </MenuItem>
      </Select>
    </FormControl>
  );
}
